<template>
	<div class="imageUploadForm w-full" @click="fileTagClick($event)">
		<div class="text-center" ref="imageEmpty">
			<label
				class="w-full h-full m-0 p-0 border-2 border-solid border-gray-400"
				v-bind:for="tempId"
				ref="fileLabel"
			>
				<font-awesome-icon icon="fa-solid fa-plus" class="w-full h-full cursor-pointer" />
			</label>
			<input
				type="file"
				ref="file"
				v-bind:id="tempId"
				v-bind:class="classNm"
				name="moImg"
				style="display: none; margin-top: 0"
				v-bind:value="formatValue"
				v-bind:size="maxSize"
				@change="onChange($event)"
				v-bind:accept="'image/*'"
			/>
		</div>
		<div style="display: none; border: 2px solid gray" ref="imageExist" class="text-right">
			<img
				src=""
				@error="noImage256"
				v-bind:class="classNm + 'Src'"
				alt=""
				style="width: 100%; height: 100%"
			/>
			<button
				type="button"
				class="btn btn-red m-0"
				style="width: 100%; border-radius: 0"
				@click="deleteImg($event)"
			>
				삭제
			</button>
		</div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
	props: {
		param: {
			type: Object,
			default: function () {
				return {
					imgType: 'mainImg', // mainImg/addImg
					index: 0,
					imgUrl: '',
				};
			},
		},
	},
	data() {
		return {
			// 파라미터
			tempId: '',
			classNm: 'addImg',
			getImgType: '', // mainImg/addImg
			getIndex: 0,
			getImgUrl: '',
			formatValue: null,
			maxSize: 50 * 1024 * 1024, // 50MB
			acceptType: 'image/jpeg,image/jpg,image/png,application/pdf',
		};
	},
	methods: {
		fileTagClick: function (e) {
			if ($(this.$refs.imageEmpty).css('display') != 'none') {
				$(this.$refs.fileLabel).click();
			}
		},
		// chage 이벤트
		onChange: function ($event) {
			let $this = this;
			if ($event.target.files && $event.target.files.length > 0) {
				if ($event.target.files[0].size > this.maxSize) {
					alert('파일 용량이 올바르지 않습니다.\n확인 후, 다시 업로드 하세요.');
					$event.target.value = '';
					return false;
				}
				if (this.$webUtils.isNotNull(this.acceptType)) {
					let chkType = this.acceptType.split(',');
					let isChk = false;
					if (chkType.length > 0) {
						for (let i = 0; i < chkType.length; i++) {
							let tempStr = '';
							if (chkType[i].indexOf('/') > -1) {
								tempStr = chkType[i].substr(chkType[i].indexOf('/') + 1);
							} else {
								tempStr = chkType[i];
							}
							if ($event.target.value.indexOf(tempStr.trim()) > -1) {
								isChk = true;
								break;
							}
						}
					}
					if (isChk == false) {
						alert('파일 확장자명이 올바르지 않습니다.\n확인 후, 다시 업로드 하세요.');
						$event.target.value = '';
						return false;
					}
				}
				this.$eventBus.$emit('getCropper', {
					event: $event,
					id: $this.tempId,
					imgType: $this.getImgType,
				});
			}
		},
		deleteImg: function (e) {
			e.stopPropagation();
			let $this = this;
			$($this.$refs.imageEmpty).show();
			$($this.$refs.imageExist).hide();
			$($this.$refs.imageExist).children().first().attr('src', '');
			$($this.$refs.imageExist).children().first().attr('alt', '');
			$this.$refs.file.value = '';
			alert('저장 버튼을 누르면 삭제된 이미지가 정상적으로 적용됩니다.');
			this.$eventBus.$emit('callBackDeleteImg', {
				imgType: $this.getImgType,
				index: $this.getIndex,
				imgUrl: $this.getImgUrl,
			});
		},
		noImage256(e) {
			this.getNoImage256(e);
		},
	},
	computed: mapGetters(['getServerUrl', 'getFilePrefix']),
	mounted() {
		let $this = this;
		$(this.$refs.fileLabel).on('click', function (e) {
			e.stopPropagation();
		});
	},
	created() {
		let $this = this;
		$this.getImgType = $this.param.imgType;
		$this.getIndex = $this.param.index;
		$this.getImgUrl = '';
		$this.tempId = $this.$webUtils.getUniqueId();
		$this.classNm = $this.getImgType;
		this.$eventBus.$off('callBackCroppedImage').$on('callBackCroppedImage', function (param) {
			if (param.id == $this.tempId) {
				$($this.$refs.imageEmpty).hide();
				$($this.$refs.imageExist).show();
				$($this.$refs.imageExist).children().first().attr('src', param.image);
				$($this.$refs.imageExist).children().first().attr('alt', '');
				this.$eventBus.$emit('callBackImageReg', {
					imgType: $this.getImgType,
					index: $this.getIndex,
					imgUrl: param.file,
				});
			}
		});
		this.$eventBus.$off('initImage').$on('initImage', function (param) {
			$this.getImgUrl = '';
			$($this.$refs.imageEmpty).children('input').val('');
			$($this.$refs.imageExist).children().first().attr('src', '');
			$($this.$refs.imageExist).children().first().attr('alt', '');
			$($this.$refs.imageEmpty).show();
			$($this.$refs.imageExist).hide();
		});
		this.$eventBus.$off('setImage').$on('setImage', function (param) {
			if (this.$webUtils.isNull(param.basicExtNm)) return false;

			if ($this.getImgType == 'mainImg') {
				$this.getImgUrl = this.$webUtils.nvl(param.basicExtNm, '');
			}

			if (this.$webUtils.isNotNull($this.getImgUrl)) {
				$($this.$refs.imageEmpty).hide();
				$($this.$refs.imageExist).show();

				let imgUrl = $this.getServerUrl + $this.getFilePrefix + $this.getImgUrl;
				$($this.$refs.imageExist).children().first().attr('src', imgUrl);
				$($this.$refs.imageExist).children().first().attr('alt', $this.getImgUrl);
				// http://localhost:8150
			}
		});
		this.$eventBus.$off('backCropper').$on('backCropper', function (id) {
			if (id == $this.tempId) {
				$this.$refs.file.value = '';
				$($this.$refs.fileLabel).click();
			}
		});
		this.$eventBus.$off('exitCropper').$on('exitCropper', function (id) {
			if (id == $this.tempId) {
				$this.$refs.file.value = '';
			}
		});
	},
};
</script>

<style></style>
