<template>
	<div class="grid grid-cols-12">
		<label class="col-span-12 session-title">기본정보</label>
		<!-- 기본정보 1열 -->
		<div class="form col-span-12">
			<div class="form-content-left">
				<div class="w-per3 pr-3">
					<span class="text-lg">상품명</span>
				</div>
				<div class="w-per9">
					<input
						type="text"
						id="prdNm"
						class="form-control md:form-control2"
						v-model="productEntity.prdNm"
					/>
				</div>
			</div>

			<div class="form-content-right">
				<div class="w-per3 pr-3">
					<span class="text-lg">상품코드</span>
				</div>
				<div class="w-per9">
					<input
						type="text"
						id="prdCd"
						class="form-control md:form-control2"
						v-model="productEntity.prdCd"
					/>
				</div>
			</div>
		</div>

		<!-- 기본정보 2열 -->
		<div class="form col-span-12">
			<div class="form-content-left">
				<div class="w-per3 pr-3">
					<span class="text-lg">브랜드</span>
				</div>
				<div class="w-per9">
					<input
						type="text"
						id="brandNm"
						class="form-control md:form-control2"
						v-model="brandEntity.mainNmKr"
						disabled
					/>
				</div>
			</div>

			<div class="form-content-right">
				<div class="w-per3 pr-3">
					<span class="text-lg">판매업체</span>
				</div>
				<div class="w-per9 text-center">
					<input
						type="text"
						id="selAcntNm"
						class="form-control md:form-control2"
						v-model="selAcntEntity.selAcntNm"
						disabled
					/>
					<!--              <input type="text" class="form-control md:form-control2" :value="''">-->
				</div>
			</div>
		</div>

		<!-- 기본정보 3열 -->
		<div class="form col-span-12">
			<div class="w-per12 text-right flex items-center mb-4">
				<div id="stdCtgrNo3" slot-scope="" class="w-per3 pr-3">
					<span class="text-lg">카테고리</span>
				</div>
				<div class="w-per7 pr-1 md:pr-3">
					<select
						class="form-control"
						v-model="stdCtgrTreeDTO.stdCtgrNo1"
						v-on:change="
							setCtgrList(2, stdCtgrTreeDTO.stdCtgrNo1);
							stdCtgrTreeDTO.stdCtgrNo2 = '';
							stdCtgrTreeDTO.stdCtgrNo3 = '';
						"
					>
						<option :value="''">선택</option>
						<option
							v-for="item in code.ctgr1List"
							:key="item.stdCtgrNo"
							:value="item.stdCtgrNo"
							v-text="item.stdCtgrNm"
						></option>
					</select>
				</div>
				<div class="w-per7 pr-1 md:pr-3">
					<select
						class="form-control"
						v-model="stdCtgrTreeDTO.stdCtgrNo2"
						v-on:change="
							setCtgrList(3, stdCtgrTreeDTO.stdCtgrNo2);
							stdCtgrTreeDTO.stdCtgrNo3 = '';
						"
						:disabled="stdCtgrTreeDTO.stdCtgrNo1 == ''"
					>
						<option :value="''">선택</option>
						<option
							v-for="item in code.ctgr2List"
							:key="item.stdCtgrNo"
							:value="item.stdCtgrNo"
							v-text="item.stdCtgrNm"
						></option>
					</select>
				</div>
				<div class="w-per7">
					<select
						class="form-control"
						v-model="stdCtgrTreeDTO.stdCtgrNo3"
						:disabled="stdCtgrTreeDTO.stdCtgrNo2 == ''"
					>
						<option :value="''">선택</option>
						<option
							v-for="item in code.ctgr3List"
							:key="item.stdCtgrNo"
							:value="item.stdCtgrNo"
							v-text="item.stdCtgrNm"
						></option>
					</select>
				</div>
			</div>
		</div>

		<label class="col-span-12 session-title mt-9">판매정보</label>
		<!-- 판매정보 1열 -->
		<div class="form col-span-12">
			<div class="form-content-left">
				<div class="w-per3 pr-3">
					<span class="text-lg">정상가</span>
				</div>
				<div class="w-per9">
					<input
						type="text"
						id="normPrc"
						class="form-control md:form-control2"
						v-on:input="priceEntity.normPrc = $webUtils.toOnlyNumber(priceEntity.normPrc)"
						v-model="priceEntity.normPrc"
					/>
				</div>
			</div>

			<div class="form-content-right w-per6">
				<div class="w-per3 pr-3">
					<span class="text-lg">판매가</span>
				</div>
				<div class="w-per9">
					<input
						type="text"
						id="selPrc"
						class="form-control md:form-control2"
						v-on:input="priceEntity.selPrc = $webUtils.toOnlyNumber(priceEntity.selPrc)"
						v-model="priceEntity.selPrc"
					/>
				</div>
			</div>
		</div>

		<!-- 판매정보 2열 -->
		<div class="form col-span-12">
			<div class="form-content-left">
				<div class="w-per3 pr-3">
					<span class="text-lg">부가세</span>
				</div>
				<div class="w-per9">
					<div class="flex justify-center">
						<div class="form-check-inline">
							<input
								class="radio-inline"
								type="radio"
								name="vtaxTypCd"
								value="01"
								v-model="productEntity.vtaxTypCd"
							/>
							<label class="radio-label">과세</label>
						</div>
						<div class="form-check-inline">
							<input
								class="radio-inline"
								type="radio"
								name="vtaxTypCd"
								value="02"
								v-model="productEntity.vtaxTypCd"
							/>
							<label class="radio-label">비과세</label>
						</div>
					</div>
				</div>
			</div>

			<div class="form-content-right">
				<div class="w-per3 pr-3">
					<span class="text-lg">환불구분</span>
				</div>
				<div class="w-per9">
					<div class="flex justify-center">
						<div class="form-check-inline">
							<input
								class="radio-inline"
								type="radio"
								name="refundYn"
								value="Y"
								v-model="productEntity.refundYn"
							/>
							<label class="radio-label">Y</label>
						</div>
						<div class="form-check-inline">
							<input
								class="radio-inline"
								type="radio"
								name="refundYn"
								value="N"
								v-model="productEntity.refundYn"
							/>
							<label class="radio-label">N</label>
						</div>
					</div>
				</div>
			</div>
		</div>

		<label class="col-span-12 session-title mt-9">배송정보</label>
		<!-- 배송정보 테이블 -->
		<div id="dlvTmpltNm" class="form col-span-12">
			<div class="inline-block md:ml-20">
				<table class="table-fixed w-full table-form">
					<thead>
						<tr>
							<th class="w-full text-lg text-left md:pl-3" colspan="5">배송템플릿</th>
							<!--                <th class="w-per2"></th>-->
							<!--                <th class="w-per3"></th>-->
							<!--                <th class="w-per2"></th>-->
							<!--                <th class="w-per3"></th>-->
						</tr>
					</thead>
					<tbody>
						<tr>
							<td rowspan="5" class="text-center">
								배송템플릿명<br /><span v-show="deliveryEntity.dlvTmpltNm != ''"
									>({{ deliveryEntity.dlvTmpltNm }})</span
								>
							</td>
							<td>배송구분</td>
							<td>{{ prdGroupEntity.dlvTypNm }}</td>
							<td>매입구분</td>
							<td>{{ prdGroupEntity.bizTypNm }}</td>
						</tr>
						<tr>
							<td>정산구분</td>
							<td>{{ prdGroupEntity.sttlTypNm }}</td>
							<td>사입유형</td>
							<td>{{ prdGroupEntity.pchsTypNm }}</td>
						</tr>
						<tr>
							<td>배송택배</td>
							<td>{{ deliveryEntity.dlvCmpnyNm }}</td>
							<td>배송비(편도)</td>
							<td>{{ deliveryEntity.dlvAmt | thousandFormat }}</td>
						</tr>
						<tr>
							<td>반품택배</td>
							<td>{{ deliveryEntity.rtrnCmpnyNm }}</td>
							<td>반품비(택배)</td>
							<td>{{ deliveryEntity.rtrnAmt | thousandFormat }}</td>
						</tr>
						<tr>
							<td>반품주소</td>
							<!--                <td colspan="3">(우 : {{prdDlvVo.dlvRtrnAmtInfo.rtrnZipCd}}) {{prdDlvVo.dlvRtrnAmtInfo.rtrnAddrDtl}}</td>-->
							<td
								colspan="3"
								v-text="
									deliveryEntity.deliveryAddrEntityList.length > 0
										? `${deliveryEntity.deliveryAddrEntityList[1].addrMain} ${deliveryEntity.deliveryAddrEntityList[1].addrDtl}`
										: ''
								"
							></td>
						</tr>
					</tbody>
				</table>
			</div>
		</div>

		<label class="col-span-12 session-title mt-9">옵션정보</label>
		<div class="form col-span-12">
			<div class="block md:ml-20">
				<simpleTableComponent
					:itemListParam="stockEntityList"
					:prdNo="prdNo"
					@stock-callback="stockCallback"
				></simpleTableComponent>
			</div>
		</div>
	</div>
</template>

<script>
import simpleTableComponent from '@/utils/vueUtils/simpleTableComponent';
import { mixinPrdDetailPolicy } from '@/utils/jsUtils/mixinPrdDetailPolicy.js';
// eslint-disable-next-line prettier/prettier
import { productApi, categoryApi, brandApi, dlvTempltApi, prdGroupApi, selAcntApi } from '@/api/index.js';

export default {
	name: 'ProductDetailPolicy',
	components: { simpleTableComponent },
	mixins: [mixinPrdDetailPolicy],
	watch: {
		prdNo: {
			immediate: true,
			handler: function (newVal, oldVal) {
				if (this.$webUtils.isNotNull(newVal) && String(newVal) != '0') {
					this.isReg = false;
				} else {
					this.isReg = true;
				}
				if (!this.isReg) {
					this.selectProductDetail(newVal);
				} else {
					// 초기화 필요
					this.initPrdDetailPolicyParams();
					this.$nextTick(() => {
						brandApi.selectBrandInfoApi(this.$store.state.brandNo).then(response => {
							this.brandEntity = this.$webUtils.deepCopyObject(response.data);
						});
						prdGroupApi.selectPrdGroupInfoApi(this.$store.state.prdGroupNo).then(response => {
							this.prdGroupEntity = this.$webUtils.deepCopyObject(response.data);
						});
						selAcntApi.selectSelAcntInfoApi(this.$store.state.selAcntNo).then(response => {
							this.selAcntEntity = this.$webUtils.deepCopyObject(response.data);
							dlvTempltApi.selectDlvTmpltInfoApi(response.data.dlvTmpltSeq).then(response => {
								this.deliveryEntity = this.$webUtils.deepCopyObject(response.data);
							});
						});
						this.setCtgrList(1);
					});
				}
			},
		},
	},
	props: {
		prdNo: {
			type: String,
			default: '',
		},
	},
	data() {
		return {
			isReg: true, // 처음 등록 여부
			pageTap: '01',
		};
	},
	methods: {
		// 316996735L
		async selectProductDetail(prdNo) {
			await productApi
				.selectProductDetailApi(prdNo)
				.then(response => {
					this.productEntity = this.$webUtils.deepCopyObject(response.data.productEntity);
					this.selAcntEntity = this.$webUtils.deepCopyObject(response.data.selAcntEntity);
					this.brandEntity = this.$webUtils.deepCopyObject(response.data.brandEntity);
					this.prdGroupEntity = this.$webUtils.deepCopyObject(response.data.prdGroupEntity);
					this.stdCtgrTreeDTO = this.$webUtils.deepCopyObject(response.data.stdCtgrTreeDTO);
					this.priceEntity = this.$webUtils.deepCopyObject(response.data.priceEntity);
					this.deliveryEntity = this.$webUtils.deepCopyObject(response.data.deliveryEntity);
					this.stockEntityList = JSON.parse(JSON.stringify(response.data.stockEntityList));

					this.setCtgrList(1, 0, this.stdCtgrTreeDTO.siteCd);
					this.setCtgrList(2, this.stdCtgrTreeDTO.stdCtgrNo1, this.stdCtgrTreeDTO.siteCd);
					this.setCtgrList(3, this.stdCtgrTreeDTO.stdCtgrNo2, this.stdCtgrTreeDTO.siteCd);
					this.$emit('prddetail-policy-callback', {
						isFirstRegYn: 'N',
						pageTap: this.pageTap,
						prdNo: prdNo,
						prdStatCd: this.productEntity.prdStatCd,
						prdSelCd: this.productEntity.prdSelCd,
					});
				})
				.catch(error => {
					console.log(error);
				});
		},
		stockCallback(result) {
			this.stockEntityList = JSON.parse(JSON.stringify(result));
			this.savePrdDetailPolicyFn();
		},
		async savePrdDetailPolicyFn() {
			let params = {
				productEntity: this.productEntity,
				selAcntEntity: this.selAcntEntity,
				brandEntity: this.brandEntity,
				prdGroupEntity: this.prdGroupEntity,
				stdCtgrTreeDTO: this.stdCtgrTreeDTO,
				priceEntity: this.priceEntity,
				deliveryEntity: this.deliveryEntity,
				stockEntityList: this.stockEntityList,
			};
			await productApi.savePrdDetailPolicyApi(JSON.parse(JSON.stringify(params))).then(response => {
				let prdNo = String(response.data.prdNo);
				if (this.isReg) {
					let url = window.location.href;
					let idx = url.lastIndexOf('/');
					let newUrl = url.substring(0, idx + 1) + prdNo;
					history.replaceState(null, null, newUrl);
					this.isReg = false;
					this.$nextTick(() => {
						this.$emit('prddetail-policy-callback', {
							isFirstRegYn: 'Y',
							pageTap: this.pageTap,
							prdNo: String(prdNo),
							prdStatCd: this.productEntity.prdStatCd,
							prdSelCd: this.productEntity.prdSelCd,
						});
					});
				}
			});
		},
	},
	mounted() {},
	created() {
		const $this = this;
		this.$eventBus.$off('savePrdDetailPolicy').$on('savePrdDetailPolicy', param => {
			// 유효성 검사
			if (!this.isValidProductDetailPolicy()) return false;
			$this.$eventBus.$emit('getStockListInfo');
		});
	},
	destroyed() {},
};
</script>

<style></style>
