<template>
	<div class="grid grid-cols-12">
		<imageCropperComponent></imageCropperComponent>
		<label class="col-span-12 session-title">상품 이미지</label>
		<!-- 1열 상품 이미지  -->
		<div class="form col-span-12">
			<div class="form-content-center-full">
				<div class="w-full">
					<div class="w-40">
						<imageUploadComponent
							v-bind:param="{ imgType: 'mainImg', index: 0 }"
						></imageUploadComponent>
					</div>
				</div>
			</div>
		</div>

		<label class="col-span-12 session-title mt-9 required">상품설명</label>
		<div id="prdDescCont" class="form col-span-12">
			<div class="mb-3">
				<button
					type="button"
					class="btn mr-2"
					:class="[prdDescEntity.prdDescTypCd == '01' ? 'btn-emerald' : 'btn-gray ']"
					@click="prdDescEntity.prdDescTypCd = '01'"
				>
					Text Mode
				</button>
				<button
					type="button"
					class="btn btn-emerald"
					:class="[prdDescEntity.prdDescTypCd == '01' ? 'btn-gray' : 'btn-emerald']"
					@click="prdDescEntity.prdDescTypCd = '02'"
				>
					Editor Mode
				</button>
			</div>
			<div class="form-content-left-full" v-if="prdDescEntity.prdDescTypCd == '01'">
				<!-- prdDescContClob: "<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/B_1.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_01.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_02.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_03.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_04.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_05.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_06.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_07.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_08.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_09.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_10.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_11.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_12.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_13.jpg\" />\n<img src=\"http://sjg.wilord.co.kr/outBanner/WM/DEAL/half/20211105_HF_45/D_14.jpg\" />" -->
				<!-- prdDescTypCd: "02" -->
				<div class="w-full min-h-200">
					<textarea
						v-model="prdDescEntity.prdDescContClob"
						class="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
						rows="8"
						placeholder=""
					></textarea>
				</div>
			</div>

			<div class="form-content-left-full" v-if="prdDescEntity.prdDescTypCd == '02'">
				<div class="w-full block text-left min-h-200">
					<vueEditorComponent
						:editorId="editorId"
						:editorContent="prdDescEntity.prdDescContClob"
						@editor-callback="editorCallback"
					></vueEditorComponent>
				</div>
			</div>
		</div>

		<label class="col-span-12 session-title mt-9 required">원산지</label>
		<div class="col-span-12 md:flex">
			<div class="form-group w-per12 md:w-per6 md:mr-3">
				<label class="control-label">구분</label>
				<select
					id="orgnTypCd"
					class="form-control mb-0"
					name="orgnTypCd"
					v-model="prdDescEntity.orgnTypCd"
					@change="
						prdDescEntity.orgnTypCd == '01'
							? (prdDescEntity.orgnCd = 'KR')
							: (prdDescEntity.orgnCd = '')
					"
				>
					<option value="01">국내</option>
					<option value="02">해외</option>
				</select>
			</div>
			<div class="form-group w-per12 md:w-per6">
				<label class="control-label">상세</label>
				<select
					id="orgnCd"
					name="orgnCd"
					v-model="prdDescEntity.orgnCd"
					class="form-control"
					v-bind:disabled="prdDescEntity.orgnTypCd != '02'"
				>
					<option value="">선택</option>
					<option
						v-for="item in code.nationList"
						:key="item.value"
						:value="item.value"
						v-text="item.name"
						v-show="!(prdDescEntity.orgnTypCd == '02' && item.value == 'KR')"
					></option>
				</select>
			</div>
		</div>

		<label class="col-span-12 session-title mt-9">속성정보</label>
		<div class="form col-span-12">
			<div class="form-content-left-full">
				<div class="w-per2 pr-3">
					<span class="text-lg required">성별</span>
				</div>
				<div id="selectedGenderList" class="w-per10">
					<v-container class="flex">
						<v-checkbox
							class="mt-0 mr-3"
							v-for="item in code.genderList"
							:key="item.value"
							v-model="selectedGenderList"
							:label="item.name"
							:value="item.value"
							hide-details
						></v-checkbox>
					</v-container>
				</div>
			</div>
		</div>
		<div class="form col-span-12">
			<div class="form-content-left-full">
				<div class="w-per2 pr-3">
					<span class="text-lg required">시즌</span>
				</div>
				<div id="selectedSeasonList" class="w-per10">
					<v-container class="inline-block text-left">
						<span v-for="item in code.seasonList" :key="item.value" class="inline-flex">
							<v-checkbox
								class="mt-0 mr-3"
								v-model="selectedSeasonList"
								:label="item.name"
								:value="item.value"
								hide-details
							></v-checkbox>
						</span>
					</v-container>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import 'vuetify/dist/vuetify.css';
import { mixinPrdDetailContents } from '@/utils/jsUtils/mixinPrdDetailContents.js';
import { productApi } from '@/api/index.js';
import vueEditorComponent from '@/utils/vueUtils/vueEditorComponent.vue';
import imageUploadComponent from '@/utils/vueUtils/imageUploadComponent.vue';
import imageCropperComponent from '@/utils/vueUtils/imageCropperComponent.vue';

export default {
	name: 'ProductDetailContents',
	components: {
		vueEditorComponent,
		imageUploadComponent,
		imageCropperComponent,
	},
	mixins: [mixinPrdDetailContents],
	watch: {
		'prdDescEntity.prdDescTypCd': {
			immediate: true,
			handler: function (newVal, oldVal) {
				this.prdDescEntity.prdDescTypCd = newVal ?? '01';
			},
		},
		prdNo: {
			immediate: true,
			handler: function (newVal, oldVal) {
				if (this.$webUtils.isNotNull(newVal) && String(newVal) != '0') {
					this.getPrdContentsInfo().then(response => {
						this.$emit('prddetail-contents-callback', {
							prdNo: String(this.prdNo),
							prdStatCd: response.prdStatCd,
							prdSelCd: response.prdSelCd,
						});
						this.$eventBus.$emit('setImage', this.prdImgEntity);
					});
				} else {
					this.initPrdDetailContentsParams();
				}
			},
		},
	},
	props: {
		prdNo: {
			type: String,
			default: '',
		},
		pageTap: {
			type: String,
			default: '',
		},
	},
	data() {
		return {
			editorId: '',
		};
	},
	methods: {
		async getPrdContentsInfo() {
			this.getCodeList();
			this.initPrdDetailContentsParams();
			return await productApi.getPrdContentsInfoApi(this.prdNo).then(reponse => {
				this.productEntity = this.$webUtils.deepCopyObject(reponse.data.productEntity);
				if (!(this.productEntity.prdStatCd == '01' || this.productEntity.prdStatCd == '02')) {
					this.prdImgEntity = this.$webUtils.deepCopyObject(reponse.data.prdImgEntity);
					this.prdDescEntity = this.$webUtils.deepCopyObject(reponse.data.prdDescEntity);
					this.genderList = this.$webUtils.deepCopyArray(reponse.data.genderList);
					this.seasonList = this.$webUtils.deepCopyArray(reponse.data.seasonList);
					this.selectedGenderList = this.$webUtils.deepCopyArray(reponse.data.selectedGenderList);
					this.selectedSeasonList = this.$webUtils.deepCopyArray(reponse.data.selectedSeasonList);
				}
				return this.productEntity;
			});
		},
		getCodeList() {
			this.getNationList();
			this.getGenderList();
			this.getSeasonList();
		},
		editorCallback(result) {
			if (result.id == this.editorId) {
				this.prdDescEntity.prdDescContClob = result.content;
			}
		},
		async savePrdContentsInfo() {
			let params = {
				productEntity: this.productEntity,
				prdImgEntity: this.$webUtils.deepCopyObject(this.prdImgEntity),
				prdDescEntity: this.prdDescEntity,
				genderList: this.genderList,
				seasonList: this.seasonList,
				selectedGenderList: this.selectedGenderList,
				selectedSeasonList: this.selectedSeasonList,
			};

			let formData = new FormData();
			if (this.$webUtils.isNotNull(this.imgSource.mainImgSource)) {
				formData.append('fileMainImg', this.imgSource.mainImgSource);
			}

			formData.append('metaData', JSON.stringify(params));
			await productApi.savePrdDetailContentsApi(formData).then(reponse => {
				this.getPrdContentsInfo().then(() => {
					this.$eventBus.$emit('setImage', this.prdImgEntity);
				});
			});
		},
	},
	mounted() {
		this.$eventBus.$emit('initImage');
		// if (this.$webUtils.isNotNull(this.prdNo) && this.prdNo != '0') {
		// 	this.getPrdContentsInfo();
		// }
	},
	created() {
		let $this = this;
		this.editorId = this.$webUtils.getUniqueId();
		this.$eventBus.$off('goPrdDetailContents').$on('goPrdDetailContents', param => {
			this.getPrdContentsInfo();
		});
		this.$eventBus.$off('savePrdDetailContents').$on('savePrdDetailContents', param => {
			// 유효성 검사
			if (!this.isValidProductDetailContents()) return false;
			this.savePrdContentsInfo();
		});
		this.$eventBus.$off('callBackImageReg').$on('callBackImageReg', function (result) {
			if (result.imgType == 'mainImg') {
				$this.imgSource.mainImgSource = result.imgUrl;
				$this.prdImgEntity.mainMode = 'C';
			}
		});
		this.$eventBus.$off('callBackDeleteImg').$on('callBackDeleteImg', function (result) {
			if (result.imgType == 'mainImg') {
				// 이미지 업로드가 늘어나면 해당 로직을 수정해야함
				$this.imgSource.mainImgSource = null;
				$this.prdImgEntity.mainMode = 'D';
			}
		});
	},
};
</script>

<style>
.v-input--selection-controls {
	margin-top: 0;
	padding-top: 0;
}
.v-input__control label {
	margin-bottom: 0;
}
</style>
