export let mixinPrdDetailPolicy = {
	data() {
		return {
			productEntityArg: {
				prdNo: '',
				prdNm: '',
				prdCd: '',
				prdStatCd: '',
				prdSelCd: '',
				createDt: '',
				createNo: '',
				updateDt: '',
				updateNo: '',
				vtaxTypCd: '01',
				refundYn: 'Y',
				siteCd: '1',
			},
			productEntity: {
				prdNo: '',
				prdNm: '',
				prdCd: '',
				prdStatCd: '',
				prdSelCd: '',
				createDt: '',
				createNo: '',
				updateDt: '',
				updateNo: '',
				vtaxTypCd: '',
				refundYn: '',
				siteCd: '',
			},
			selAcntEntity: {
				selAcntNo: '',
				selAcntNm: '',
				mrgnRt: '',
				empNo: '',
			},
			brandEntity: {
				brandNo: '',
				brandCd: '',
				hgrnkBrandCd: '',
				mainNmEn: '',
				mainNmKr: '',
				siteCd: '',
			},
			prdGroupEntity: {
				prdGroupNo: '',
				prdGroupCd: '',
				prdGroupNm: '',
				bizTypCd: '',
				bizTypNm: '',
				brandNo: '',
				brdCd: '',
				centerGubun: '',
				createDt: '',
				createNo: '',
				defaultMdNo: '',
				dlvTypCd: '',
				dlvTypNm: '',
				mrgnRt: '',
				pchsTypCd: '',
				pchsTypNm: '',
				registNm: '',
				registNo: '',
				selAcntNo: '',
				siteCd: '',
				sttlTypCd: '',
				sttlTypNm: '',
				updateDt: '',
				updateNo: '',
				useYn: '',
				wmsYn: '',
			},
			stdCtgrTreeDTO: {
				stdCtgrNo1: '',
				stdCtgrNo2: '',
				stdCtgrNo3: '',
				stdCtgrNm1: '',
				stdCtgrNm2: '',
				stdCtgrNm3: '',
				siteCd: '',
			},
			priceEntityArg: {
				prdNo: '',
				prdPrcNo: '',
				mrgnRt: '',
				normPrc: '',
				selPrc: '',
				createDt: '',
				createNo: '',
				updateDt: '',
				updateNo: '',
			},
			priceEntity: {
				prdNo: '',
				prdPrcNo: '',
				mrgnRt: '',
				normPrc: '',
				selPrc: '',
				createDt: '',
				createNo: '',
				updateDt: '',
				updateNo: '',
			},
			deliveryEntity: {
				dlvTmpltNm: '',
				dlvTmpltSeq: '',
				dlvTmpltType: '',
				deliveryAddrEntityList: [],
				dlvAmt: '',
				dlvCmpnyAcctCd: '',
				dlvCmpnyCd: '',
				dlvCmpnyCrctCd: '',
				dlvCmpnyNm: '',
				exchngAmt: '',
				freeDlvMinAmt: '',
				jejuYn: '',
				outskrtsAmtIslnd: '',
				outskrtsAmtJeju: '',
				rtripDlvFeeYn: '',
				rtrnAmt: '',
				rtrnBalnTypCd: '',
				rtrnBizRegNo: '',
				rtrnBoxSpecCd: '',
				rtrnBoxUnitCost: '',
				rtrnCmpnyCd: '',
				rtrnCmpnyCrctCd: '',
				rtrnCmpnyNm: '',
				selAcntNo: '',
				telNo: '',
			},
			stockEntityList: [],
		};
	},
	methods: {
		initPrdDetailPolicyParams() {
			this.productEntity = this.$webUtils.deepCopyObject(this.productEntityArg);

			for (let item in this.selAcntEntity) {
				this.selAcntEntity[item] = initByType(item, this.selAcntEntity[item]);
			}

			for (let item in this.brandEntity) {
				this.brandEntity[item] = initByType(item, this.brandEntity[item]);
			}

			for (let item in this.prdGroupEntity) {
				this.prdGroupEntity[item] = initByType(item, this.prdGroupEntity[item]);
			}

			for (let item in this.stdCtgrTreeDTO) {
				this.stdCtgrTreeDTO[item] = initByType(item, this.stdCtgrTreeDTO[item]);
			}

			this.priceEntity = this.$webUtils.deepCopyObject(this.priceEntityArg);

			for (let item in this.deliveryEntity) {
				this.deliveryEntity[item] = initByType(item, this.deliveryEntity[item]);
			}

			this.stockEntityList.splice(0);

			function initByType(key, value) {
				if (key) {
					value = '';
				} else if (
					typeof value == 'object' &&
					(key.indexOf('Array') >= 0 || key.indexOf('List') >= 0)
				) {
					value = [];
				} else if (typeof value == 'object') {
					value = {};
				} else {
					value = '';
				}
				return value;
			}
		},
		isValidProductDetailPolicy() {
			let isValid = true;
			isValid = this.isValidItem(this.productEntity.siteCd, '사이트', 'siteCd');
			if (!isValid) return false;
			isValid = this.isValidItem(this.productEntity.prdNm, '상품명', 'prdNm');
			if (!isValid) return false;
			isValid = this.isValidItem(this.productEntity.prdCd, '상품코드', 'prdCd');
			if (!isValid) return false;

			isValid = this.isValidItem(this.brandEntity.mainNmKr, '브랜드', 'brandNm');
			if (!isValid) return false;
			isValid = this.isValidItem(this.selAcntEntity.selAcntNm, '판매업체', 'selAcntNm');
			if (!isValid) return false;

			isValid = this.isValidItem(this.stdCtgrTreeDTO.stdCtgrNo3, '카테고리', 'stdCtgrNo3');
			if (!isValid) return false;

			isValid = this.isValidItem(this.priceEntity.normPrc, '정상가', 'normPrc');
			if (!isValid) return false;
			isValid = this.isValidItem(this.priceEntity.selPrc, '판매가', 'selPrc');
			if (!isValid) return false;
			if (Number(this.priceEntity.normPrc) <= 0) {
				alert('정상가는 0보다 커야합니다.');
				$('#normPrc').focus();
				isValid = false;
				return false;
			}
			if (Number(this.priceEntity.selPrc) <= 0) {
				alert('판매가는 0보다 커야합니다.');
				$('#selPrc').focus();
				isValid = false;
				return false;
			}
			if (Number(this.priceEntity.selPrc) > Number(this.priceEntity.normPrc)) {
				alert('판매가는 정상가 보다 클 수 없습니다.');
				$('#selPrc').focus();
				isValid = false;
				return false;
			}

			isValid = this.isValidItem(this.deliveryEntity.dlvTmpltSeq, '배송템플릿', 'dlvTmpltNm');
			if (!isValid) return false;

			return isValid;
		},
		isValidItem(item, itemNm, id) {
			if (this.$webUtils.isNull(item)) {
				alert(itemNm + '은(는) 필수항목입니다.');
				let tagName = $('#' + id)
					?.prop('tagName')
					?.toLowerCase();
				if (tagName == 'input' || tagName == 'select') {
					$('#' + id).focus();
				} else if (tagName == 'div') {
					$('#' + id)
						.get(0)
						.scrollIntoView({ block: 'center' });
				}
				return false;
			} else {
				return true;
			}
		},
	},
};
