<template>
	<div
		id="cropperComponent"
		style="
			display: none;
			position: fixed;
			z-index: 300;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #000;
		"
	>
		<div id="cropperContent" class="col-xs-12" style="position: relative">
			<div class="col-xs-12 text-center">
				<div id="imageWrapper" class="form-horizontal text-center" style="display: inline-block">
					<div id="imageContainer">
						<img id="image" />
					</div>
				</div>
			</div>
			<div
				id="cropFooterWrapper"
				class="w-full p-7 text-center flex items-center justify-between"
				style="
					background-color: #fff;
					position: absolute;
					z-index: 95;
					height: 55px;
					vertical-align: middle;
				"
			>
				<!--<button type="button" class="btn" @click="backBtn">돌아가기</button>-->
				<button
					type="button"
					class="btn btn_close cursor-pointer"
					@click="exitBtn"
					style="width: 25px; padding: 0; background-color: #fff"
				>
					<img src="../../assets/img/btn_close.png" alt="나가기" />
				</button>
				<button
					type="button"
					class="btn btn_return cursor-pointer"
					@click="refreshBtn"
					style="width: 36px; padding: 0; background-color: #fff"
				>
					<img src="../../assets/img/btn_return.png" alt="새로고침" />
				</button>
				<button
					type="button"
					class="btn btn_save cursor-pointer"
					@click="saveBtn"
					style="width: 25px; padding: 0; background-color: #fff"
				>
					<img src="../../assets/img/btn_save.png" alt="저장" />
				</button>
			</div>
		</div>
	</div>
</template>

<script>
import 'cropperjs/dist/cropper.css';
import Cropper from 'cropperjs';

export default {
	name: 'imageCropperComponent',
	data() {
		return {
			cropper: null,
			id: null,
			fileNm: null,
		};
	},
	methods: {
		closePopComp: function (isEventListener) {
			this.cropper.destroy();
			$('#cropperComponent').hide();
			$('body').css({ overflow: 'visible' });
			$('dim').removeClass('active');
			window.removeEventListener('popstate', this.bindPopComp);
			if (isEventListener == false) {
				this.$nextTick(function () {
					history.back();
				});
			}
		},
		backBtn: function () {
			this.$eventBus.$emit('backCropper', this.id);
			this.closePopComp(false);
		},
		exitBtn: function () {
			this.$eventBus.$emit('exitCropper', this.id);
			this.closePopComp(false);
		},
		refreshBtn: function () {
			if (this.cropper != null) {
				this.cropper.reset();
			}
		},
		saveBtn: function () {
			var $this = this;
			this.cropper.getCroppedCanvas().toBlob(function (blob) {
				var file = new File([blob], $this.fileNm);
				$this.$eventBus.$emit('callBackCroppedImage', {
					image: $this.cropper.getCroppedCanvas().toDataURL('image/png'),
					id: $this.id,
					file: file,
				});
				$this.closePopComp(false);
			});
		},
		bindPopComp: function (e) {
			e.preventDefault();
			e.stopPropagation();
			this.$eventBus.$emit('exitCropper', this.id);
			this.closePopComp(true);
		},
	},
	created: function () {
		var $this = this;
		this.$eventBus.$off('getCropper').$on('getCropper', function (param) {
			history.pushState(null, 'cropper', window.location.href);
			window.addEventListener('popstate', $this.bindPopComp);
			$('body').css({ overflow: 'hidden' });
			$('dim').addClass('active');
			$('#cropperComponent').show();
			$('#imageWrapper').empty();
			var imageContainerElement = document.createElement('div');
			imageContainerElement.id = 'imageContainer';
			var imgElement = document.createElement('img');
			imgElement.id = 'image';
			imageContainerElement.append(imgElement);
			$('#imageWrapper').append(imageContainerElement);

			$('#imageContainer').css('margin-top', '5px');
			var componentHeight = $('#cropperComponent').height() - 55;
			$('#cropFooterWrapper').css('top', componentHeight + 'px');

			var $event = param.event;
			$this.cropper = null;
			$this.id = param.id;
			$this.fileNm = $event.target.files[0].name;
			var image = document.getElementById('image');
			var $image = $(image);
			var reader = new FileReader();
			reader.readAsDataURL($event.target.files[0]);
			reader.onload = function (event) {
				$image.attr('src', event.target.result);

				var imageTest = new Image();
				imageTest.src = event.target.result;
				imageTest.onload = function () {
					var containerHeight = $('#imageContainer').height();
					var containerWidth = $('#imageContainer').width();
					var correctedRatio = containerWidth / this.width;
					var correctedImageHeight = this.height * correctedRatio; // 화면에 보이는 조정된 높이

					if (componentHeight > containerHeight) {
						// top 조절
						$('#imageContainer').css('margin-top', (componentHeight - containerHeight) / 2 + 'px');
					} else {
						// containerHeight 확장
						$('#imageContainer').css('margin-top', '5px');
					}

					// if (correctedImageHeight > componentHeight) {
					// 	// 원본 이미지가 길 경우 스크롤 가능하게 처리
					// 	$('#imageContainer').css('height', componentHeight + 'px');
					// }

					$this.$nextTick(function () {
						if ($this.cropper == null) {
							$this.cropper = new Cropper(image, {
								viewMode: 3,
								// 가로 이미지 등록 시 1/2 비율로 자른다.
								aspectRatio: param.imgType == 'hztlImg' ? 2 / 1 : 1,
								autoCropArea: 0.9,
								zoomable: false,
								zoomOnTouch: true,
								dragMode: 'move',
								crop(event) {},
							});
						}
					});
				};
			};
		});
	},
};
</script>

<style>
@media (min-width: 768px) {
	#cropperContent {
		margin-left: 15%;
		margin-right: 15%;
	}
}
</style>
