export let mixinPrdDetailContents = {
	data() {
		return {
			productEntityArg: {
				prdNo: '',
				prdNm: '',
				prdCd: '',
				pcode: '',
				prdRegCd: '',
				prdSelCd: '',
				prdStatCd: '',
				selAcntNo: '',
				brandNo: '',
				mdNo: '',
				prdGroupNo: '',
				certiTypCd: '',
				createDt: '',
				createNm: '',
				createNo: '',
				selYr: '',
				siteCd: '1',
				stdCtgrNo: '',
				useYn: 'Y',
			},
			productEntity: {
				prdNo: '',
				prdNm: '',
				prdCd: '',
				pcode: '',
				prdRegCd: '',
				prdSelCd: '',
				prdStatCd: '',
				selAcntNo: '',
				brandNo: '',
				mdNo: '',
				prdGroupNo: '',
				certiTypCd: '',
				createDt: '',
				createNm: '',
				createNo: '',
				selYr: '',
				siteCd: '1',
				stdCtgrNo: '',
				useYn: 'Y',
			},
			prdImgEntity: {
				prdNo: '',
				basicExtNm: '',
				filePath: '',
				createDt: '',
				createNm: '',
				createNo: '',
				updateDt: '',
				updateNm: '',
				updateNo: '',
				mainMode: '',
			},
			prdDescEntityArg: {
				prdNo: '',
				orgnTypCd: '01',
				orgnTypNm: '',
				orgnCd: 'KR',
				orgnNm: '',
				prdDescTypCd: '02',
				prdDescContClob: '',
				createDt: '',
				createNm: '',
				createNo: '',
				updateDt: '',
				updateNm: '',
				updateNo: '',
			},
			prdDescEntity: {
				prdNo: '',
				orgnTypCd: '01',
				orgnTypNm: '',
				orgnCd: 'KR',
				orgnNm: '',
				prdDescTypCd: '02',
				prdDescContClob: '',
				createDt: '',
				createNm: '',
				createNo: '',
				updateDt: '',
				updateNm: '',
				updateNo: '',
			},
			genderList: [],
			seasonList: [],
			selectedGenderList: [],
			selectedSeasonList: [],
			imgSource: { mainImgSource: '' },
		};
	},
	methods: {
		initPrdDetailContentsParams() {
			this.productEntity = this.$webUtils.deepCopyObject(this.productEntityArg);

			for (let item in this.prdImgEntity) {
				this.prdImgEntity[item] = initByType(item, this.prdImgEntity[item]);
			}

			this.prdDescEntity = this.$webUtils.deepCopyObject(this.prdDescEntityArg);

			this.genderList.splice(0);
			this.seasonList.splice(0);
			this.selectedGenderList.splice(0);
			this.selectedSeasonList.splice(0);

			for (let item in this.imgSource) {
				this.imgSource[item] = initByType(item, this.imgSource[item]);
			}

			function initByType(key, value) {
				if (typeof value == 'object' && (key.indexOf('Array') >= 0 || key.indexOf('List') >= 0)) {
					value = [];
				} else if (typeof value == 'object') {
					value = {};
				} else {
					if (key == 'prdDescTypCd') {
						value = '02';
					} else if (key == 'orgnTypCd') {
						value = '01';
					} else if (key == 'orgnCd') {
						value = 'KR';
					} else {
						value = '';
					}
				}
				return value;
			}
		},
		isValidProductDetailContents() {
			let isValid = true;
			if (
				this.$webUtils.isNull(this.productEntity.prdNo) ||
				String(this.productEntity.prdNo) == '0'
			) {
				alert('상품정보가 없습니다.');
				isValid = false;
				return false;
			}
			isValid = this.isValidItem(this.prdDescEntity.prdDescContClob, '상품설명', 'prdDescCont');
			if (!isValid) return false;
			isValid = this.isValidItem(this.prdDescEntity.orgnTypCd, '원산지', 'orgnTypCd');
			if (!isValid) return false;
			isValid = this.isValidItem(this.prdDescEntity.orgnCd, '원산지 상세', 'orgnCd');
			if (!isValid) return false;

			if (Array.isArray(this.selectedGenderList) && this.selectedGenderList.length == 0) {
				alert('성별을 하나 이상 선택해주세요.');
				$('#selectedGenderList').get(0).scrollIntoView({ block: 'center' });
				isValid = false;
				return false;
			}

			if (Array.isArray(this.selectedSeasonList) && this.selectedSeasonList.length == 0) {
				alert('시즌을 하나 이상 선택해주세요.');
				$('#selectedSeasonList').get(0).scrollIntoView({ block: 'center' });
				isValid = false;
				return false;
			}

			return isValid;
		},
		isValidItem(item, itemNm, id) {
			if (this.$webUtils.isNull(item)) {
				alert(itemNm + '은(는) 필수항목입니다.');
				let tagName = $('#' + id)
					?.prop('tagName')
					?.toLowerCase();
				if (tagName == 'input' || tagName == 'select') {
					$('#' + id).focus();
				} else if (tagName == 'div') {
					$('#' + id)
						.get(0)
						.scrollIntoView({ block: 'center' });
				}
				return false;
			} else {
				return true;
			}
		},
	},
};
