<template>
	<vue-editor :id="id" v-model="content" @change="submitEditorContent()"></vue-editor>
</template>

<script>
import { VueEditor } from 'vue2-editor';
export default {
	name: 'vueEditorComponent',
	components: {
		VueEditor,
	},
	watch: {
		editorContent: {
			immediate: true,
			handler: function (newVal, oldVal) {
				this.content = newVal ?? '';
			},
		},
		editorId: {
			immediate: true,
			handler: function (newVal, oldVal) {
				this.id = newVal ?? '';
			},
		},
		content: {
			immediate: false,
			handler: function (newVal, oldVal) {
				this.content = newVal ?? '';
				this.submitEditorContent();
			},
		},
	},
	props: {
		editorId: {
			type: String,
			default: '',
		},
		editorContent: {
			type: String,
			default: '',
		},
	},
	data() {
		return {
			id: '',
			content: '',
		};
	},
	methods: {
		submitEditorContent() {
			const $this = this;
			this.$emit('editor-callback', {
				id: $this.id,
				content: String($this.content),
			});
		},
	},
	mounted() {},
	created() {},
};
</script>

