<template>
	<v-card class="">
		<div>
			<v-simple-table
				style="max-height: 300px"
				class="overflow-y-scroll overflow-x-scroll whitespace-nowrap"
			>
				<template v-slot:default>
					<thead>
						<tr>
							<th class="text-center"></th>
							<th class="text-center">재고번호</th>
							<th class="text-center">옵션코드</th>
							<th class="text-center table-input" v-if="isReg">
								<input
									id="optItemNmMst1"
									type="text"
									class="form-control2 md:form-control"
									v-model="optItemNmMst1"
									placeholder="옵션명1"
								/>
							</th>
							<th class="text-center" v-else>{{ optItemNmMst1 }}</th>
							<th class="text-center table-input" v-if="dubleOptYn == 'Y' && isReg">
								<input
									id="optItemNmMst2"
									type="text"
									class="form-control2 md:form-control"
									v-model="optItemNmMst2"
									placeholder="옵션명2"
								/>
							</th>
							<th class="text-center" v-else-if="dubleOptYn == 'Y' && !isReg">
								{{ optItemNmMst2 }}
							</th>
							<th class="text-center">옵션가</th>
							<th class="text-center">재고</th>
							<th class="text-center">사용여부</th>
						</tr>
					</thead>
					<tbody>
						<tr v-for="item in itemList" :key="item.stockNo">
							<td>
								<v-checkbox
									v-model="item.check"
									color="success"
									:value="true"
									hide-details
									style="padding-top: 0 !important; margin-top: 0 !important"
									:class="!item.isNew ? 'v-input--is-disabled' : ''"
								></v-checkbox>
							</td>
							<td class="text-center" v-text="item.isNew ? '' : item.stockNo"></td>
							<td class="text-center">{{ item.optValueArf }}</td>
							<!--        <td>{{ item.optItemNm1 }}</td>-->
							<td
								class="text-center table-input"
								v-for="(optValue, index) in item.optionValueEntityList"
								:key="optValue.optItemNo"
								v-show="index != 1 || (index == 1 && dubleOptYn == 'Y')"
							>
								<span v-if="!item.isNew">{{ optValue.optValueNm }}</span>
								<span v-else
									><input
										type="text"
										class="form-control2 md:form-control"
										v-model="optValue.optValueNm"
								/></span>
							</td>
							<td class="text-center table-input">
								<span v-if="!item.isNew">{{ item.addPrc | thousandFormat }}</span>
								<span v-else
									><input
										type="text"
										class="form-control2 text-right"
										v-on:input="item.addPrc = $webUtils.toOnlyNumber(item.addPrc)"
										v-model="item.addPrc"
								/></span>
							</td>
							<td class="text-right table-input">
								<span v-if="!item.isNew">{{ item.realStckQty | thousandFormat }}</span>
								<span v-else
									><input
										type="text"
										class="form-control2 text-right"
										v-on:input="item.realStckQty = $webUtils.toOnlyNumber(item.realStckQty)"
										v-model="item.realStckQty"
								/></span>
							</td>
							<td class="text-center">
								<span v-if="!item.isNew">{{ item.useYn }}</span>
								<span v-else
									><select class="form-control" v-model="item.useYn">
										<option value="Y">Y</option>
										<option value="N">N</option>
									</select>
								</span>
							</td>
						</tr>
					</tbody>
				</template>
			</v-simple-table>
		</div>
		<div class="mt-5 mb-5 md:pr-5 flex justify-end">
			<button
				type="button"
				class="btn mr-2"
				:class="dubleOptYn == 'N' ? 'btn-emerald' : 'btn-gray'"
				v-show="isReg"
				@click="changeDubleOpt()"
			>
				{{ dubleOptYn == 'N' ? '2단 옵션 등록' : '2단 옵션 취소' }}
			</button>
			<button type="button" class="btn btn-emerald mr-2" @click="addOption()">추가</button>
			<button type="button" class="btn btn-red mr-2" @click="deleteOption()">삭제</button>
		</div>
	</v-card>
</template>

<script>
export default {
	name: 'simpleTableComponent',
	props: {
		prdNo: {
			type: String,
			default: '',
		},
		itemListParam: {
			type: Array,
			default: function () {
				return [];
			},
		},
	},
	watch: {
		itemListParam: {
			deep: true, // 하위 요소 변경 체크
			immediate: true,
			handler: function (newVal, oldVal) {
				const temp = JSON.parse(JSON.stringify(newVal));
				this.itemList = this.setOptItemInfo(temp);
			},
		},
		prdNo: {
			immediate: true,
			handler: function (newVal, oldVal) {
				if (this.$webUtils.isNull(newVal) || String(newVal) == '0') {
					this.dubleOptYn = 'N';
					this.isReg = true;
					this.optItemNmMst1 = '';
					this.optItemNmMst2 = '';
				} else {
					this.isReg = false;
				}
			},
		},
		dubleOptYn: {
			immediate: true,
			handler: function (newVal, oldVal) {},
		},
	},
	data() {
		return {
			itemList: [],
			dubleOptYn: 'N', // 이단 옵션 여부
			optItemNmMst1: '',
			optItemNmMst2: '',

			isReg: true, // 처음 등록 여부
			// stockNo: '',
			// optStockCd: '',
			// optItemNm1: '',
			// optItemNm2: '',
			// optValueNm1: '',
			// optValueNm2: '',
			// addPrc: 0,
			// realStckQty: 0,
			// tempStckQty: 0,
			// useYn: 'Y',
		};
	},
	methods: {
		checkDubleOptYn() {
			if (this.itemList.length > 0) {
				let idx = 0;
				for (let item of this.itemList) {
					item.idx = idx++;
					item.check = item.check ?? false;
				}
			}
		},
		setOptItemInfo(p_itemList) {
			if (p_itemList.length > 0) {
				let idx = 0;
				for (let item of p_itemList) {
					item.idx = idx++;
					item.check = item.check ?? false;
					item.isNew = item.isNew ?? false;
				}
				this.optItemNmMst1 = p_itemList[0].optionItemEntityList[0].optItemNm;
				if (p_itemList[0].optionItemEntityList.length > 1) {
					this.optItemNmMst2 = p_itemList[0].optionItemEntityList[1].optItemNm;
					this.dubleOptYn = 'Y';
				}
			} else {
				this.dubleOptYn = 'N';
			}
			return p_itemList;
		},
		changeDubleOpt() {
			if (this.dubleOptYn == 'Y') {
				this.dubleOptYn = 'N';
				if (this.itemList.length > 0) {
					this.itemList.forEach(item => {
						item.optionItemEntityList.splice(1);
						item.optionValueEntityList.splice(1);
					});
				}
			} else if (this.dubleOptYn == 'N') {
				this.dubleOptYn = 'Y';
				if (this.itemList.length > 0) {
					this.itemList.forEach(item => {
						item.optionItemEntityList.push({
							prdNo: '',
							optItemNo: 2,
							optItemNm: this.optItemNmMst2,
							optPrrtRnk: 2,
						});
						item.optionValueEntityList.push({
							prdNo: '',
							stockNo: '',
							optItemNo: 2,
							optValueNm: '',
						});
					});
				}
			}
		},
		addOption() {
			let optionItemEntityList = [
				{ prdNo: '', optItemNo: 1, optItemNm: this.optItemNmMst1, optPrrtRnk: 1 },
			];

			let optionValueEntityList = [{ prdNo: '', stockNo: '', optItemNo: 1, optValueNm: '' }];

			if (this.dubleOptYn == 'Y') {
				optionItemEntityList.push({
					prdNo: '',
					optItemNo: 2,
					optItemNm: this.optItemNmMst2,
					optPrrtRnk: 2,
				});
				optionValueEntityList.push({ prdNo: '', stockNo: '', optItemNo: 2, optValueNm: '' });
			}

			let stockNoTemp = Math.random();

			let item = {
				addPrc: 0,
				optValueArf: '',
				optionItemEntityList: optionItemEntityList,
				optionValueEntityList: optionValueEntityList,
				prdNo: '',
				realStckQty: '',
				selAcntNo: '',
				stockNo: `temp${stockNoTemp}`,
				tempStckQty: '',
				useYn: 'Y',
				check: false,
				isNew: true,
			};
			this.itemList.push(item);
		},
		deleteOption() {
			for (let idx = this.itemList.length - 1; idx >= 0; idx--) {
				if (this.itemList[idx].check == true) {
					this.itemList.splice(idx, 1);
				}
			}
		},
	},
	created() {
		// this.itemList = initItemList();
		const $this = this;
		this.$eventBus.$off('getStockListInfo').$on('getStockListInfo', () => {
			let resultStockList = [];
			let isValid = true;
			if (this.isReg) {
				if ($this.$webUtils.isNull($this.optItemNmMst1)) {
					alert('옵션명1을 입력해주세요.');
					$('#optItemNmMst1').focus();
					return false;
				}
				if (this.dubleOptYn == 'Y') {
					if ($this.$webUtils.isNull($this.optItemNmMst2)) {
						alert('옵션명2을 입력해주세요.');
						$('#optItemNmMst2').focus();
						return false;
					}
				}
			}

			resultStockList = JSON.parse(JSON.stringify($this.itemList));

			if (resultStockList.length == 0) {
				alert('등록된 옵션이 없습니다. 옵션을 등록해주세요.');
				return false;
			}

			resultStockList.forEach(item => {
				if ($this.$webUtils.isNull(item.optionValueEntityList[0].optValueNm)) {
					alert(`${item.optionItemEntityList[0].optItemNm} 값은 필수항목 입니다.`);
					isValid = false;
					return false;
				}
				if (
					$this.dubleOptYn == 'Y' &&
					$this.$webUtils.isNull(item.optionValueEntityList[1].optValueNm)
				) {
					alert(`${item.optionItemEntityList[1].optItemNm} 값은 필수항목 입니다.`);
					isValid = false;
					return false;
				}
				if ($this.$webUtils.isNull(item.addPrc)) {
					alert(`옵션가는 필수항목 입니다.`);
					isValid = false;
					return false;
				}
				if ($this.$webUtils.isNull(item.realStckQty)) {
					alert(`재고는 필수항목 입니다.`);
					isValid = false;
					return false;
				}
				if (item.isNew == true) {
					item.stockNo = '';
				}
				if (this.isReg) {
					item.optionValueEntityList[0].optItemNm = $this.optItemNmMst1;
					if ($this.dubleOptYn == 'Y') {
						item.optionValueEntityList[1].optItemNm = $this.optItemNmMst2;
					}
				}
			});

			if (isValid) {
				$this.$emit('stock-callback', resultStockList);
			}
		});
	},
};
</script>

<style>
.v-input--is-disabled .v-input--selection-controls__input {
	background-color: #eee !important;
	opacity: 1 !important;
}

.table-input {
	padding-left: 0 !important;
	padding-right: 0 !important;
	min-width: 80px !important;
}
</style>
