<template>
	<div id="productDetail">
		<div class="flex text-left w-full p-4 bg-white">
			<div class="w-full">
				<div id="contentWrapper">
					<!-- pageTap 01 -->
					<ProductDetailPolicy
						v-show="pageTap == '01'"
						:prdNo="prdNo"
						@prddetail-policy-callback="prdDetailPolicyCallback"
					></ProductDetailPolicy>

					<!-- pageTap 02 -->
					<ProductDetailContents
						v-show="pageTap == '02'"
						:pageTap="pageTap"
						:prdNo="prdNo"
						@prddetail-contents-callback="prdDetailContentsCallback"
					></ProductDetailContents>

					<label class="w-full session-title mt-10"></label>
					<div class="flex justify-center mb-7">
						<button type="button" class="btn btn-red mr-3" @click="savePrdDetail()">
							<span class="text-base md:text-lg">저장</span>
						</button>
						<button
							type="button"
							class="btn mr-3"
							:class="[pageTap == '01' ? 'btn-gray' : 'btn-emerald']"
							:disabled="pageTap == '01'"
							@click="pageTap = '01'"
						>
							<span class="text-base md:text-lg">이전 페이지</span>
						</button>
						<button
							type="button"
							class="btn"
							:class="[pageTap == '01' ? 'btn-emerald' : 'btn-gray']"
							:disabled="pageTap == '02' || $webUtils.isNull(prdStatCd) || prdStatCd == '01'"
							@click="goPrdDetailContentsPage()"
						>
							<span class="text-base md:text-lg">다음 페이지</span>
						</button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import ProductDetailPolicy from '@/components/productRegister/ProductDetailPolicy';
import ProductDetailContents from '@/components/productRegister/ProductDetailContents';
import { productApi } from '@/api/index.js';

export default {
	name: 'ProductDetail',
	components: { ProductDetailPolicy, ProductDetailContents },
	watch: {
		'$route.params.prdNo': {
			immediate: true,
			handler: function (newVal, oldVal) {
				this.prdNo = String(newVal);
				if (this.$webUtils.isNull(this.prdNo) || this.prdNo == '0') {
					this.pageTap = '01';
					this.prdStatCd = '';
					this.prdSelCd = '';
				}
			},
		},
	},
	data() {
		return {
			prdNo: '',
			prdStatCd: '',
			prdSelCd: '',
			pageTap: '01',
		};
	},
	methods: {
		savePrdDetail() {
			if (this.pageTap == '01') {
				this.$eventBus.$emit('savePrdDetailPolicy');
			} else {
				this.$eventBus.$emit('savePrdDetailContents');
			}
		},
		goPrdDetailContentsPage() {
			this.pageTap = '02';
			this.$eventBus.$emit('goPrdDetailContents');
		},
		prdDetailPolicyCallback(result) {
			this.prdNo = String(result.prdNo);
			this.prdStatCd = result.prdStatCd;
			this.prdSelCd = result.prdSelCd;
			if (result.isFirstRegYn == 'Y') {
				alert('상품 컨텐츠를 계속해서 등록해주세요.');
				this.pageTap = '02';
				this.$eventBus.$emit('goPrdDetailContents');
			}
		},
		prdDetailContentsCallback(result) {
			this.prdNo = String(result.prdNo);
			this.prdStatCd = result.prdStatCd;
			this.prdSelCd = result.prdSelCd;
		},
	},
	mounted() {},
	created() {},
};
</script>

<style scoped>
@media (min-width: 768px) {
	#contentWrapper {
		/*width: 75% !important;*/
		margin-left: 15%;
		margin-right: 15%;
	}
}
</style>
